app-upsert-roles {
  /*label*/
  /*add, remove, save, and cancel buttons*/
  /* Inputs */ }
  app-upsert-roles label {
    color: #000000; }
  app-upsert-roles .add-button {
    background: #67b3f5;
    text-transform: uppercase;
    font-size: medium;
    font-family: Raleway, SansSerif;
    display: block;
    width: 175px;
    align-items: center;
    color: white;
    font-weight: bold;
    margin: auto; }
  app-upsert-roles .remove-button {
    background: lightcoral;
    text-transform: uppercase;
    font-size: medium;
    font-family: Raleway, SansSerif;
    display: block;
    width: 125px;
    height: 40px;
    align-items: center;
    color: white;
    margin-top: 0.5em; }
  app-upsert-roles .addGroupButton {
    background: #67b3f5;
    text-transform: uppercase;
    display: block;
    width: 175px;
    align-items: center;
    font-family: Raleway, SansSerif;
    color: white;
    font-weight: bold;
    margin: auto;
    margin-top: 2em; }
  app-upsert-roles .addGroupButton:hover {
    background: #459fe3;
    text-transform: uppercase;
    display: block;
    width: 175px;
    align-items: center;
    font-family: Raleway, SansSerif;
    color: white;
    font-weight: bold;
    margin: auto;
    margin-top: 2em; }
  app-upsert-roles .update-group-button {
    font-size: 0.25em;
    cursor: pointer;
    transition: .3s transform;
    color: #4d4d4d;
    text-align: center;
    padding-top: 1.5em; }
  app-upsert-roles .update-group-button:hover {
    text-decoration: none;
    font-weight: bold;
    color: #EAA00C; }
  app-upsert-roles .remove-group-button {
    font-size: 0.25em;
    cursor: pointer;
    transition: .3s transform;
    color: #4d4d4d;
    text-align: center;
    padding-top: 1.5em; }
  app-upsert-roles .remove-group-button:hover {
    text-decoration: none;
    font-weight: bold;
    color: #EAA00C; }
  app-upsert-roles .properties {
    background: #e6e6e6;
    display: flex;
    flex-direction: column; }
  app-upsert-roles .flex-column {
    display: flex;
    flex-direction: column; }
  app-upsert-roles .full-width {
    width: 100%; }
  app-upsert-roles input::-webkit-input-placeholder {
    text-transform: uppercase;
    color: #bbb; }
  app-upsert-roles textarea:focus, app-upsert-roles input:focus, app-upsert-roles select:focus {
    outline: none;
    border: 2px solid #999; }
  app-upsert-roles select, app-upsert-roles input[type="text"] {
    flex: 1;
    margin-top: 0;
    margin-bottom: 1.2em;
    padding: .5em;
    color: #666;
    font-weight: bold; }
  app-upsert-roles input[type="text"] {
    flex-basis: 20px;
    margin-top: 2px; }
  app-upsert-roles input[type="text"] {
    padding: .7em 1em !important;
    font-size: 0.7em;
    display: block; }
  app-upsert-roles .bottom-space {
    margin-bottom: 2em; }
  app-upsert-roles .top-space {
    margin-top: 2em; }
  app-upsert-roles .disabled {
    background: #c3c3c3;
    cursor: default;
    border: none; }
