app-clients-wrapper .column {
  font-size: 18px;
  padding: 0 2.5em;
  transition: opacity 1.5s; }

app-clients-wrapper .apps {
  font-size: 1.8em;
  margin-bottom: 2em; }

app-clients-wrapper mat-list > div > span {
  font-size: 16px; }

app-clients-wrapper h2 {
  color: #4d4d4d;
  font-weight: bold;
  line-height: 1.3em;
  font-family: 'Raleway'; }

app-clients-wrapper a:link, app-clients-wrapper a:hover, app-clients-wrapper a:visited {
  text-decoration: none;
  color: #4d4d4d; }

app-clients-wrapper a:hover {
  text-decoration: underline; }

app-clients-wrapper .orange {
  color: #EAA00C; }

app-clients-wrapper .bold {
  font-weight: bold; }

app-clients-wrapper .app-item {
  border-bottom: 1px dashed #aaa;
  padding: .6em 0; }

app-clients-wrapper .create-app-button {
  background: #EAA00C;
  text-transform: uppercase;
  margin: auto;
  display: block;
  width: 300px; }

app-clients-wrapper .remove-app-button {
  padding: 16px 0;
  font-size: 0.25em;
  cursor: pointer;
  transition: .3s transform; }

app-clients-wrapper .remove-app-button:hover {
  text-decoration: none;
  font-weight: bold;
  color: #EAA00C;
  transform: scale(1.5); }

app-clients-wrapper .app-name {
  display: inline-block !important; }

app-clients-wrapper .app-name:hover {
  text-decoration: none;
  color: #EAA00C; }

app-clients-wrapper .container {
  margin: auto;
  width: 75%; }
