app-left-nav a, app-left-nav i {
  color: #fff; }

app-left-nav a:link, app-left-nav a:visited {
  text-decoration: none;
  color: inherit; }

app-left-nav > div {
  cursor: pointer;
  transition: .4s background; }

app-left-nav div:focus {
  outline: none;
  border: 0; }

app-left-nav .selected {
  background-color: #eef3f6; }

app-left-nav .selected a, app-left-nav .selected i {
  color: #4d4d4d; }

app-left-nav > div:hover {
  background-color: #f8fdff; }

app-left-nav > div:hover a, app-left-nav > div:hover i {
  color: #4d4d4d; }

app-left-nav > div:hover a {
  text-decoration: none; }

app-left-nav .item-icon {
  padding: 11px;
  margin-left: 4px;
  display: inline-block; }

app-left-nav .item-icon i {
  width: 24px; }

app-left-nav .item-name {
  font-size: 0.85em;
  font-weight: bold; }
