app-dashboard-dialog {
  font-family: sans-serif; }
  app-dashboard-dialog .close-dialog-wrapper {
    position: relative; }
  app-dashboard-dialog .close-dialog-border {
    position: absolute;
    width: 100%;
    z-index: 10; }
  app-dashboard-dialog .close-dialog {
    float: right;
    cursor: pointer; }
  app-dashboard-dialog .close-dialog-border {
    background: #fff;
    border-bottom: 1px solid #e6e6e6; }
  app-dashboard-dialog .row {
    clear: both; }
  app-dashboard-dialog h2 {
    text-align: left;
    font-weight: normal;
    margin: 1.2em;
    color: #424242; }
  app-dashboard-dialog .content {
    overflow: auto;
    padding-top: 24px;
    height: calc(100% - 24px); }
