app-scope-form {
  /* Inputs */ }
  app-scope-form input::-webkit-input-placeholder {
    text-transform: uppercase;
    color: #bbb; }
  app-scope-form textarea:focus, app-scope-form input:focus, app-scope-form select:focus {
    outline: none;
    border: 2px solid #999; }
  app-scope-form select, app-scope-form input[type="text"] {
    flex: 1;
    margin-top: 0;
    margin-bottom: 1.2em;
    padding: .5em;
    color: #666;
    font-weight: bold; }
  app-scope-form input[type="text"] {
    flex-basis: 20px;
    margin-top: 2px; }
  app-scope-form input[type="text"] {
    padding: .7em 1em !important;
    font-size: 0.7em;
    display: block; }
