app-dashboard-dialog-footer .actions {
  margin: 1.5em 0; }

app-dashboard-dialog-footer .flex-row {
  margin: 1.2em 0; }

app-dashboard-dialog-footer .flex-row {
  display: flex;
  flex-direction: row; }

app-dashboard-dialog-footer .cancel-button, app-dashboard-dialog-footer .update-config-button {
  transition: .25s box-shadow;
  border-radius: 2px;
  cursor: pointer; }

app-dashboard-dialog-footer .cancel-button:hover, app-dashboard-dialog-footer .update-config-button:hover {
  /* from: https://codepen.io/hansmaad/pen/XNGWXy */
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
  transition: box-shadow 0.28s cubic-bezier(0.4, 0, 0.2, 1); }

app-dashboard-dialog-footer .update-config-button {
  background: #00abe6;
  text-transform: uppercase;
  display: block;
  width: 30%;
  line-height: 52px;
  color: #fff;
  font-weight: bold; }

app-dashboard-dialog-footer .update-config-button.disabled {
  background: #94dcf5;
  box-shadow: none !important;
  cursor: default !important; }

app-dashboard-dialog-footer .cancel-button {
  text-transform: uppercase;
  line-height: 52px;
  width: 30%;
  font-weight: bold;
  color: #616161;
  background: #e6e6e6;
  margin-right: 1.5em;
  display: block; }

app-dashboard-dialog-footer .cancel-button, app-dashboard-dialog-footer .update-config-button {
  float: left; }

app-dashboard-dialog-footer .property-name {
  flex-basis: 20%;
  margin-right: 2em;
  justify-content: flex-end;
  padding-top: .5em;
  text-align: right;
  font-size: .9em;
  font-weight: bold; }

app-dashboard-dialog-footer .property-field {
  flex-basis: calc(65% - 1em);
  color: #424242; }

app-dashboard-dialog-footer .center {
  text-align: center; }
