app-tenant-selector-component .column {
  font-size: 18px;
  padding: 0 2.5em;
  transition: opacity 1.5s; }

app-tenant-selector-component app-tenant-selector-component {
  width: 100%;
  padding: 0 50px; }

app-tenant-selector-component .tenant-item {
  padding: 5px;
  border-radius: 7px;
  transition: .25s background; }

app-tenant-selector-component .mat-card {
  width: 200px;
  height: 100px;
  padding: 1em 1em 1em 1em;
  margin: 2em 2em 0 0;
  border: 4px solid white;
  cursor: pointer; }

app-tenant-selector-component .add-item {
  width: 240px;
  height: 140px;
  padding: 2.5em 1em 2.5em 1em;
  margin: 2em 2em 0 0;
  border-radius: 7px;
  background-color: white;
  border: 4px solid white;
  box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  transition: .25s background; }

app-tenant-selector-component .mat-button {
  font-size: 18px;
  border-radius: 0px;
  background-color: #61b861;
  color: white;
  min-width: 220px;
  height: 76px; }

app-tenant-selector-component mat-list a {
  border-bottom: 1px dashed #aaa;
  padding: .6em 0; }

app-tenant-selector-component .register-message {
  color: #fff;
  margin: 4em 0 1em 0; }

app-tenant-selector-component h2 {
  text-transform: uppercase;
  color: #fff;
  font-weight: bold;
  line-height: 1.3em;
  font-family: 'Raleway'; }

app-tenant-selector-component a {
  cursor: pointer; }

app-tenant-selector-component a:link, app-tenant-selector-component a:hover, app-tenant-selector-component a:visited {
  text-decoration: none;
  color: #000; }

app-tenant-selector-component .home-button-blue {
  color: #459fe3;
  font-weight: bolder; }

app-tenant-selector-component .bold {
  font-weight: bold; }

app-tenant-selector-component .register {
  display: flex;
  align-items: center;
  justify-content: space-around; }

app-tenant-selector-component .full-width {
  width: 100%; }

app-tenant-selector-component .float-left {
  float: left; }

app-tenant-selector-component .float-right {
  float: right; }

app-tenant-selector-component .circle {
  width: 20px;
  height: 20px;
  background: #4cb8ab;
  border-radius: 50%;
  padding-left: 4px;
  padding-top: 4px;
  color: #fff;
  position: absolute;
  right: -14px;
  top: -14px;
  opacity: 0; }

app-tenant-selector-component .circle .material-icons {
  font-size: 15px !important; }

app-tenant-selector-component .tenant-item.selected-org, app-tenant-selector-component .selected-add-new {
  border-color: #4cb8ab;
  background-color: #c5efea; }

app-tenant-selector-component .circle.selected-org {
  opacity: 1; }
