  /* Inputs */ }
  app-upsert-permissions label {
    color: #000000; }
  app-upsert-permissions .add-button {
    background: #67b3f5;
    text-transform: uppercase;
    font-size: medium;
    font-family: Raleway, SansSerif;
    display: block;
    width: 175px;
    align-items: center;
    color: white;
    font-weight: bold;
    margin: auto; }
  app-upsert-permissions .remove-button {
    background: lightcoral;
    text-transform: uppercase;
    font-size: medium;
    font-family: Raleway, SansSerif;
    display: block;
    width: 125px;
    height: 40px;
    align-items: center;
    color: white;
    margin-top: 0.5em; }
  app-upsert-permissions .addGroupButton {
    background: #67b3f5;
    text-transform: uppercase;
    display: block;
    width: 175px;
    align-items: center;
    font-family: Raleway, SansSerif;
    color: white;
    font-weight: bold;
    margin: auto;
    margin-top: 2em; }
  app-upsert-permissions .addGroupButton:hover {
    background: #459fe3;
    text-transform: uppercase;
    display: block;
    width: 175px;
    align-items: center;
    font-family: Raleway, SansSerif;
    color: white;
    font-weight: bold;
    margin: auto;
    margin-top: 2em; }
  app-upsert-permissions .update-group-button {
    font-size: 0.25em;
    cursor: pointer;
    transition: .3s transform;
    color: #4d4d4d;
    text-align: center;
    padding-top: 1.5em; }
  app-upsert-permissions .update-group-button:hover {
    text-decoration: none;
    font-weight: bold;
    color: #EAA00C; }
  app-upsert-permissions .remove-group-button {
    font-size: 0.25em;
    cursor: pointer;
    transition: .3s transform;
    color: #4d4d4d;
    text-align: center;
    padding-top: 1.5em; }
  app-upsert-permissions .remove-group-button:hover {
    text-decoration: none;
    font-weight: bold;
    color: #EAA00C; }
  app-upsert-permissions .properties {
    background: #e6e6e6;
    display: flex;
    flex-direction: column; }
  app-upsert-permissions .flex-column {
    display: flex;
    flex-direction: column; }
  app-upsert-permissions .full-width {
    width: 100%; }
  app-upsert-permissions input::-webkit-input-placeholder {
    color: #bbb; }
  app-upsert-permissions textarea:focus, app-upsert-permissions input:focus, app-upsert-permissions select:focus {
    outline: none;
    border: 2px solid #999; }
  app-upsert-permissions select, app-upsert-permissions input[type="text"] {
    flex: 1;
    margin-top: 0;
    margin-bottom: 1.2em;
    padding: .5em;
    color: #666;
    font-weight: bold; }
  app-upsert-permissions input[type="text"] {
    flex-basis: 20px;
    margin-top: 2px; }
  app-upsert-permissions input[type="text"] {
    padding: .7em 1em !important;
    font-size: 0.7em;
    display: block; }
  app-upsert-permissions .bottom-space {
    margin-bottom: 2em; }
  app-upsert-permissions .top-space {
    margin-top: 2em; }
  app-upsert-permissions .disabled {
    background: #c3c3c3;
    cursor: default;
    border: none; }
  app-upsert-permissions mat-select {
    width: 200px;
    float: left;
    margin-top: .3em;
    margin-bottom: .1em;
    background: #fff;
    height: 32px;
    border: 1px solid #cccccc; }
  app-upsert-permissions mat-select.disabled {
    background-color: #c3c3c3; }
  app-upsert-permissions .mat-select-trigger {
    height: 100%; }
  app-upsert-permissions .mat-select-value {
    padding-top: 0.75em;
    padding-left: 1em;
    color: #666;
    font-weight: bold;
    font-size: 0.8em;
    text-align: left; }
