app-upsert-provider .left {
  text-align: left; }

app-upsert-provider .property-name {
  flex-basis: 20%;
  margin-right: 2em;
  justify-content: flex-end;
  padding-top: .5em;
  text-align: right;
  font-size: .9em;
  font-weight: bold; }

app-upsert-provider .new-provider-config > .flex-row {
  margin: 1.2em 0; }

app-upsert-provider .property-field {
  flex-basis: calc(65% - 1em);
  color: #424242; }

app-upsert-provider .property-name, app-upsert-provider .property-field {
  display: flex; }

app-upsert-provider .flex-row {
  display: flex;
  flex-direction: row; }

app-upsert-provider .scope .flex-row {
  margin-top: 0.5em;
  margin-bottom: 1.2em;
  color: #444;
  font-size: 0.9em; }

app-upsert-provider .flex-column {
  display: flex;
  flex-direction: column; }

app-upsert-provider .flex-no-grow {
  flex-grow: 0; }

app-upsert-provider .properties {
  background: #e6e6e6;
  display: flex;
  flex-direction: column;
  padding: 2em 0 0 0; }

app-upsert-provider mat-select {
  width: 200px;
  float: left;
  margin-top: .3em;
  margin-bottom: .1em;
  background: #fff;
  height: 32px;
  border: 1px solid #cccccc; }

app-upsert-provider mat-select.disabled {
  background-color: #c3c3c3; }

app-upsert-provider .select-field {
  margin: .8em 0; }

app-upsert-provider .select-field .property-name {
  margin-top: .5em; }

app-upsert-provider .app-type {
  width: 200px;
  min-height: 100px;
  padding: 1em;
  text-align: center;
  display: inline-block;
  border: 1px solid #fff;
  cursor: pointer;
  transition: .6s all;
  color: #fff;
  margin-right: 0 25px; }

app-upsert-provider .app-type.no-margin {
  margin: 0; }

app-upsert-provider .app-type .description {
  margin-top: 1em; }

app-upsert-provider input[type="text"] {
  padding: .7em 1em !important;
  font-size: 0.7em;
  display: block; }

app-upsert-provider select, app-upsert-provider input[type="text"] {
  flex: 1;
  margin-top: 0;
  margin-bottom: 1.2em;
  padding: .5em;
  color: #666;
  font-weight: bold; }

app-upsert-provider input::-webkit-input-placeholder {
  text-transform: uppercase;
  color: #bbb; }

app-upsert-provider textarea:focus, app-upsert-provider input:focus, app-upsert-provider select:focus {
  outline: none;
  border: 2px solid #999; }

app-upsert-provider .app-type:hover, app-upsert-provider .app-type.active {
  background: #ce8900; }

app-upsert-provider .flex-row input[type="text"] {
  flex-basis: 20px;
  margin-top: 2px; }

app-upsert-provider .second-radio {
  margin-left: 60px; }

app-upsert-provider .realm-row {
  margin-top: 1.2em; }

app-upsert-provider .realm-row.disabled {
  opacity: 0.5; }

app-upsert-provider input[type="radio"] {
  margin-right: 15px; }

app-upsert-provider .link {
  cursor: pointer; }

app-upsert-provider .mat-select-trigger {
  height: 100%; }

app-upsert-provider .mat-select-value {
  padding-top: 0.75em;
  padding-left: 1em;
  color: #666;
  font-weight: bold;
  font-size: 0.8em; }

app-upsert-provider .ace_editor {
  border: 1px solid lightgray;
  margin: auto;
  height: 200px;
  width: 100%; }

app-upsert-provider .scrollmargin {
  height: 80px;
  text-align: center; }

/* global style for mat-option */
.mat-option-text {
  color: #444;
  font-weight: bold;
  font-size: 0.8em; }
