app-root {
  /* You can add global styles to this file, and also import other style files */ }
  app-root #signup-popup {
    transition: 0.4s background;
    font-family: 'Raleway', sans-serif;
    color: #000;
    overflow: auto;
    height: 100%; }
  app-root .bg-img {
    position: absolute;
    bottom: 5.7%;
    left: 4.1%;
    height: 300px;
    opacity: 0.1; }
  app-root .seventy-container {
    padding: 50px 0; }
  app-root ui-view {
    width: 100%;
    height: 100%; }
  app-root html,
  app-root body {
    height: 100%; }
  app-root body {
    margin: 0;
    padding: 0; }
  app-root .mat-select-value {
    height: 32px;
    text-align: left;
    line-height: 32px;
    padding: 0 0.8em;
    font-weight: bold; }
  app-root .mat-select-value-text {
    color: #666;
    font-size: 0.8em; }
  app-root .custom-dialog-overlay {
    background: rgba(125, 125, 125, 0.9); }

.mat-dialog-container {
  padding: 0 !important; }
