app-dashboard-dialog-property .property-name {
  flex-basis: 20%;
  margin-right: 2em;
  justify-content: flex-end;
  padding-top: .5em;
  text-align: right;
  font-size: .9em;
  font-weight: bold; }

app-dashboard-dialog-property .flex-row {
  display: flex;
  flex-direction: row; }

app-dashboard-dialog-property .property-field {
  flex-basis: calc(65% - 1em);
  color: #424242; }

app-dashboard-dialog-property .property-name, app-dashboard-dialog-property .property-field {
  display: flex; }
