app-default-container .selector-head {
  width: 65%;
  height: 150px;
  margin: auto;
  background-color: white;
  box-shadow: 0px 3px #888; }

app-default-container .selector-container {
  width: 65%;
  margin: auto;
  padding: 0 0 0 0;
  background-color: #e6e6e6;
  box-shadow: 0px 3px #888; }

app-default-container .selector-container-row {
  padding: 0 0 3% 0;
  margin: 0 3% 0 3%;
  background-color: #e6e6e6; }

app-default-container .selector-footer {
  width: 65%;
  height: 100px;
  padding: 2em 0 0 0;
  margin: auto;
  background-color: #fff;
  box-shadow: 0px 3px 3px #888; }

app-default-container .footer-row {
  padding: 0 30px 0 50px;
  margin: 0 0 0 0;
  height: 76px;
  background-color: #ffffff; }

app-default-container h3 {
  padding: 2em 0 0 1.5em;
  color: #272727;
  font-weight: bolder;
  font-size: 30px;
  line-height: 1.3em;
  font-family: 'Raleway'; }
