  app-user-profile h1 {
    font-family: Raleway, SansSerif;
    font-size: 24px;
    color: black; }
  app-user-profile h1 .title {
    margin-top: 0.2em; }
  app-user-profile h1 .menu-button {
    margin-right: 1em; }
  app-user-profile h2 {
    font-family: Raleway, SansSerif;
    color: black;
    font-weight: 700; }
  app-user-profile h3 {
    font-family: Raleway, SansSerif;
    color: black; }
  app-user-profile .menu {
    color: black; }
  app-user-profile p {
    font-family: Raleway, SansSerif;
    color: #272727; }
  app-user-profile .abbreviation {
    border-radius: 50%;
    width: 50%;
    height: 25%;
    background-color: rgba(186, 94, 177, 0.58);
    margin-left: 25%;
    color: white; }
  app-user-profile .abbreviation h1 {
    padding-top: 30%;
    color: white;
    font-size: 5em;
    position: relative;
    padding-bottom: 25%;
    text-align: center; }
  app-user-profile .container {
    margin-top: 1em;
    width: 100%; }
  app-user-profile .OuterContainer {
    overflow-x: hidden;
    padding-bottom: 2em; }
  app-user-profile .InnerContainer {
    width: 100%;
    background-color: #e6e6e6;
    padding-bottom: 2em; }
  app-user-profile .InnerContainerHeader {
    margin: 0 0 0 5%; }
  app-user-profile .InnerContainerContent {
    height: 60%;
    margin: 0 0 0 10%;
    font-family: 'Raleway', 'sans-serif'; }
  app-user-profile .InnerContainerContent-left {
    width: 17.5%;
    height: 50em;
    text-align: center;
    background-color: rgba(41, 126, 190, 0.08); }
  app-user-profile .InnerContainerContent-left-dialog {
    width: 15%;
    padding-top: 2em; }
  app-user-profile .InnerContainerContent-right {
    width: 82.5%;
    margin-left: 4em;
    color: black; }
  app-user-profile .InnerContainerContent-right-dialog {
    width: 85%;
    margin-left: 4em;
    padding-top: 2em; }
  app-user-profile .InnerContainerContent-right-drop-down {
    margin: 0 0 0 2%;
    padding-left: 20px;
    border-radius: 7px; }
  app-user-profile .InnerContainerContent-left-select-users {
    width: 50%;
    margin-top: 2em;
    margin-left: 5.3em;
    text-align: justify; }
  app-user-profile .InnerContainerContent-left-select-users .list-position {
    margin-top: 1.5em; }
  app-user-profile .InnerContainerContent-right-select-users {
    width: 50%;
    margin-top: 2em;
    margin-left: 6em;
    text-align: justify; }
  app-user-profile .InnerContainerContent-right-select-users .list-position {
    margin-top: 1.5em; }
  app-user-profile select {
    height: 10em;
    width: 17.3em;
    font-size: medium;
    font-family: Raleway, SansSerif;
    color: #757575;
    padding-left: 0.7em;
    padding-top: 0.5em; }
  app-user-profile .InnerContainerContent-select-users .list-position .group-item {
    padding: 0.5em; }
  app-user-profile .InnerContainerFooter {
    height: 10%;
    margin: 0 0 0 21.5%;
    background-color: #fff; }
  app-user-profile hr {
    border: 0;
    background-color: #e6e6e6;
    height: 2px;
    margin-right: 5%; }
  app-user-profile .cancel-button {
    background: #e6e6e6;
    text-transform: uppercase;
    font-size: medium;
    font-family: Raleway, SansSerif;
    color: black;
    margin-top: 2em;
    margin-right: 10%;
    display: block;
    width: 175px;
    height: 60px;
    align-items: center; }
  app-user-profile .save-button {
    background: #61b861;
    text-transform: uppercase;
    font-size: medium;
    font-family: Raleway, SansSerif;
    margin-top: 2em;
    display: block;
    width: 175px;
    height: 60px;
    align-items: center;
    color: white; }
  app-user-profile .ButtonText {
    text-transform: uppercase; }
  app-user-profile mat-form-field {
    width: 60%;
    margin-left: 3.5em;
    margin-top: 0.5em; }
  app-user-profile #text1 {
    height: 25px;
    font-family: Raleway, SansSerif;
    font-size: medium;
    border: #666666;
    background-color: white; }
  app-user-profile mat-radio-button.assign-remove {
    padding-right: 23px; }
  app-user-profile label {
    font-weight: 900;
    color: black;
    font-family: Raleway, SansSerif; }
