  .user-wrapper .header {
    width: 100%; }
  .user-wrapper h1 {
    font-family: Raleway, SansSerif;
    font-size: 24px; }
  .user-wrapper h1 .title {
    margin-top: 0.2em;
    color: #4d4d4d; }
  .user-wrapper h1 .menu-button {
    margin-right: 1em;
    color: #4d4d4d; }
  .user-wrapper h2 {
    font-family: Raleway, SansSerif;
    font-size: 20px; }
  .user-wrapper table {
    margin-top: 1em;
    border-spacing: 0px;
    width: 100%; }
  .user-wrapper thead {
    background: #baccd5; }
  .user-wrapper th {
    text-align: left;
    font-family: Raleway, SansSerif;
    font-size: medium;
    font-weight: bold;
    color: #444444;
    line-height: 3em;
    padding-left: 1em;
    padding-right: 1em; }
  .user-wrapper tr {
    text-align: left;
    font-family: Raleway, SansSerif;
    font-size: medium;
    color: #4d4d4d; }
  .user-wrapper tr .group-name {
    font-weight: bold; }
  .user-wrapper table tr:nth-child(odd) td {
    line-height: 3em;
    background: #eef3f6;
    padding-left: 1em;
    padding-right: 1em; }
  .user-wrapper table tr:nth-child(even) td {
    line-height: 3em;
    background: #fff;
    padding-left: 1em;
    padding-right: 1em; }
  .user-wrapper .row {
    clear: both;
    width: 90%;
    margin: auto; }
  .user-wrapper .container {
    margin: auto;
    width: 90%; }
  .user-wrapper .retry {
    padding: 1em;
    display: flex;
    align-items: center;
    justify-content: space-around; }
  .user-wrapper mat-form-field {
    width: 60%;
    margin-left: 3.5em; }
  .user-wrapper #text1 {
    height: 25px;
    font-family: Raleway, SansSerif;
    font-size: medium;
    border: #666666;
    background-color: white;
    margin-top: 0.5em; }
  .user-wrapper label {
    font-weight: bold;
    color: black;
    font-family: Raleway, SansSerif; }
  .user-wrapper .ButtonText {
    text-transform: uppercase; }
  .user-wrapper .description {
    margin-top: 1.8em;
    color: #666666;
    font-family: Raleway, SansSerif; }
  .user-wrapper .levels {
    font-family: Raleway, SansSerif;
    font-size: medium; }
  .user-wrapper .addUserButton {
    background: #67b3f5;
    text-transform: uppercase;
    display: block;
    width: 175px;
    align-items: center;
    font-family: Raleway, SansSerif;
    color: white;
    font-weight: bold;
    margin: auto;
    margin-top: 2em; }
  .user-wrapper .addUserButton:hover {
    background: #459fe3;
    text-transform: uppercase;
    display: block;
    width: 175px;
    align-items: center;
    font-family: Raleway, SansSerif;
    color: white;
    font-weight: bold;
    margin: auto;
    margin-top: 2em; }
  .user-wrapper .remove-user-button {
    font-size: 0.25em;
    cursor: pointer;
    transition: .3s transform;
    color: #4d4d4d;
    text-align: center; }
  .user-wrapper .remove-user-button:hover {
    text-decoration: none;
    font-weight: bold;
    color: #EAA00C; }
  .user-wrapper .cancel-button {
    background: #e6e6e6;
    text-transform: uppercase;
    font-size: medium;
    font-family: Raleway, SansSerif;
    color: black;
    margin-top: 2em;
    margin-right: 10%;
    display: block;
    width: 175px;
    height: 60px;
    align-items: center; }
  .user-wrapper .save-button {
    background: #61b861;
    text-transform: uppercase;
    font-size: medium;
    font-family: Raleway, SansSerif;
    margin-top: 2em;
    display: block;
    width: 175px;
    height: 60px;
    align-items: center; }
  .user-wrapper .container {
    margin-top: 1em;
    width: 100%;
    border-color: #272727; }
  .user-wrapper .OuterContainer {
    overflow-x: hidden; }
  .user-wrapper .InnerContainer {
    width: 100%;
    background-color: #e6e6e6;
    padding-bottom: 2em; }
  .user-wrapper .InnerContainerHeader {
    margin: 0 0 0 5%; }
  .user-wrapper .InnerContainerContent {
    height: 60%;
    margin: 0 0 0 10%; }
  .user-wrapper .InnerContainerContent-left {
    width: 15%;
    padding-top: 2em; }
  .user-wrapper .InnerContainerContent-right {
    width: 85%;
    margin-left: 4em;
    padding-top: 2em; }
  .user-wrapper .InnerContainerContent-right-drop-down {
    margin: 0 0 0 2%;
    padding-left: 20px;
    border-radius: 7px; }
  .user-wrapper .InnerContainerContent-left-select-groups {
    width: 50%;
    margin-top: 2em;
    margin-left: 5.3em;
    text-align: justify; }
  .user-wrapper .InnerContainerContent-left-select-groups .list-position {
    margin-top: 1.5em; }
  .user-wrapper .InnerContainerContent-right-select-groups {
    width: 50%;
    margin-top: 2em;
    margin-left: 6em;
    text-align: justify; }
  .user-wrapper .InnerContainerContent-right-select-groups .list-position {
    margin-top: 1.5em; }
  .user-wrapper select {
    height: 10em;
    width: 17.3em;
    font-size: medium;
    font-family: Raleway, SansSerif;
    color: #757575;
    padding-left: 0.7em;
    padding-top: 0.5em; }
  .user-wrapper .InnerContainerContent-select-groups .list-position .group-item {
    padding: 0.5em; }
  .user-wrapper .InnerContainerFooter {
    height: 10%;
    margin: 0 0 0 21.5%;
    background-color: #fff; }
  .user-wrapper .InnerContainerContent-right-search-bar {
    width: 85%;
    margin-left: 4em; }
  .user-wrapper .searchBar {
    height: 2em;
    width: 16em;
    font-size: small;
    font-family: Raleway, SansSerif;
    color: #aaaaaa;
    padding-left: 0.8em; }
  .user-wrapper #search {
    font-size: 1em;
    padding: 0.5em;
    padding-top: 0.4em;
    padding-bottom: 0.6em;
    color: white;
    background-color: #757575; }
  .user-wrapper mat-radio-button.access-levels-buttons {
    padding-right: 25px; }
