app-clients-success {
  color: #4d4d4d; }
  app-clients-success .row {
    clear: both;
    margin-bottom: 4em; }
  app-clients-success .center {
    text-align: center; }
  app-clients-success .margin-top {
    padding-top: 3em; }
  app-clients-success .add-client-button {
    background: #67b3f5;
    text-transform: uppercase;
    display: block;
    width: 175px;
    align-items: center;
    font-family: Raleway, SansSerif;
    color: white;
    font-weight: bold;
    margin: auto; }
  app-clients-success mat-list a {
    padding: .6em 0;
    text-decoration: none;
    width: 100%; }
  app-clients-success mat-list span {
    display: block;
    float: right; }
  app-clients-success mat-list a:hover {
    color: #EAA00C;
    padding: .6em 0; }
  app-clients-success mat-list > div > span {
    font-size: 16px; }
  app-clients-success h2 button {
    float: right; }
