app-top-nav {
  width: 100%;
  float: left;
  background: #cccccc;
  height: 50px;
  box-shadow: 0px 3px 6px 0px rgba(0, 0, 0, 0.35);
  position: relative;
  display: block;
  /* avoid selection when clicked */ }
  app-top-nav .hamburger-icon, app-top-nav .notification-icon, app-top-nav .logout-icon {
    display: inline-block;
    cursor: pointer; }
  app-top-nav .hamburger-icon {
    padding: 11px;
    color: #4d4d4d;
    transition: 0.3s background; }
  app-top-nav .add-new-option {
    color: #187ac5; }
  app-top-nav .hamburger-icon:hover {
    background: #aeaeae; }
  app-top-nav .hamburger-icon, app-top-nav .notification-icon, app-top-nav .noselect {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none; }
  app-top-nav .tenant-dropdown {
    display: inline-block;
    width: calc(13.64% - 50px);
    height: 100%;
    vertical-align: middle; }
  app-top-nav .notification-icon {
    padding: 11px;
    background: #b91505;
    color: #fff;
    float: right; }
  app-top-nav .logout-icon {
    padding: 11px;
    font-size: 21px;
    width: 18px;
    float: right;
    color: #4d4d4d; }
  app-top-nav mat-select {
    margin-top: 8px; }
  app-top-nav a {
    cursor: pointer;
    color: #5a5a5a;
    transition: 0.3s color; }
  app-top-nav a:hover {
    color: #EAA00C; }
