app-blue-table table {
  width: 100%; }

app-blue-table table th {
  background: #baccd5;
  height: 45px;
  color: #333333;
  text-align: left;
  padding: 0 1em; }

app-blue-table table app-providers-list-row:nth-child(even) {
  background: #eef3f6; }

app-blue-table table app-clients-list-row:nth-child(even) {
  background: #eef3f6; }

app-blue-table table, app-blue-table tr, app-blue-table td, app-blue-table th {
  margin: 0; }

app-blue-table table span {
  cursor: pointer; }

app-blue-table table app-providers-list-row:nth-child(odd) {
  background: #fff; }

app-blue-table table td {
  text-align: center; }
