app-register-info {
  width: 100%; }
  app-register-info select {
    width: 100%;
    -webkit-appearance: none;
    -moz-appearance: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    padding: .7em 1em !important;
    font-size: 0.7em;
    background: #fff;
    border-radius: 0; }
  app-register-info input, app-register-info textarea {
    padding: .7em 1em !important;
    font-size: 0.7em; }
  app-register-info select, app-register-info input, app-register-info textarea {
    flex: 1;
    margin-top: 0;
    margin-bottom: 1.2em;
    padding: .5em;
    color: #666;
    font-weight: bold; }
  app-register-info textarea:focus, app-register-info input:focus, app-register-info select:focus {
    outline: none; }
  app-register-info textarea {
    flex: 1;
    height: 100px; }
  app-register-info .send-button {
    border: 1px solid #fff;
    border-radius: 5px;
    font-size: 1em;
    padding: .4em 1em;
    cursor: pointer;
    font-weight: bold;
    text-transform: uppercase;
    text-align: center;
    width: 50%; }
  app-register-info .tenant-item-number {
    padding-top: 2em;
    padding-bottom: 2em; }
  app-register-info .mat-stepper-horizontal {
    background: none; }
  app-register-info .divide {
    display: flex;
    flex-direction: column;
    border-bottom: 1px solid #fff;
    padding-bottom: 2em; }
  app-register-info .create-tenants {
    background: #EAA00C;
    text-transform: uppercase; }
  app-register-info .creating-orgs {
    margin-top: 2em; }
