app-org-dashboard .column {
  font-size: 18px;
  padding: 0 2.5em;
  transition: opacity 1.5s; }

app-org-dashboard app-org-dashboard {
  width: 100%;
  height: calc(100% - 50px); }

app-org-dashboard .tenant-link {
  cursor: pointer; }

app-org-dashboard .tenant-name-link {
  cursor: pointer; }

app-org-dashboard .selector {
  padding: 0 2em;
  font-size: 1.8em; }

app-org-dashboard .breadcrumb {
  color: #6e6e71;
  margin: 30px 0; }

app-org-dashboard h2 {
  font-weight: bold;
  line-height: 1.3em;
  font-family: 'Raleway';
  margin-bottom: 2em; }

app-org-dashboard a:link, app-org-dashboard a:hover, app-org-dashboard a:visited {
  text-decoration: none; }

app-org-dashboard a:hover {
  text-decoration: underline; }

app-org-dashboard .orange {
  color: #EAA00C; }

app-org-dashboard .bold {
  font-weight: bold; }

app-org-dashboard .menu-wrapper .menu {
  width: 200px;
  line-height: 2em;
  background: #6c7a8f;
  color: #fff;
  height: 100%; }

app-org-dashboard .menu-wrapper {
  width: 200px;
  height: 100%;
  float: left;
  overflow: hidden;
  transition: 0.4s width; }

app-org-dashboard .menu-wrapper.collapsed {
  width: 46px; }

app-org-dashboard .content {
  box-shadow: -8px 0 8px -8px rgba(0, 0, 0, 0.3);
  float: left;
  width: calc(100% - 250px);
  min-height: 100%;
  color: #fff;
  transition: 0.4s width; }

app-org-dashboard .content.expanded {
  width: calc(100% - 96px); }

app-org-dashboard .inner-content {
  width: calc(100% - 50px);
  margin-left: 50px; }
