app-dashboard * {
  color: #333; }

app-dashboard .title .logo img {
  height: 50px;
  margin-right: 20px; }

app-dashboard .title .content {
  font-size: 18px;
  font-weight: 600; }

app-dashboard .layout-padding {
  padding: 16px 20px; }

app-dashboard .notifications {
  background-color: #fff;
  box-shadow: 1px 1px 1px 1px #c3c3c3; }

app-dashboard .notifications .header {
  font-size: 14px;
  font-weight: 600;
  margin-bottom: 10px; }

app-dashboard .notifications .fa {
  color: #757575; }

app-dashboard .notifications .content {
  font-size: 12px;
  color: #757575; }

app-dashboard .logs .header {
  background-color: #e6e6e6;
  font-size: 14px;
  font-weight: 600; }

app-dashboard .logs .header .time {
  font-size: 12px;
  color: #666;
  margin-right: 10px; }

app-dashboard .logs .header .fa {
  color: #666; }

app-dashboard .logs .stats-item {
  border-top: .15em solid #fff;
  border-left: .15em solid #fff;
  border-bottom: .15em solid #fff; }

app-dashboard .logs .stats-item:nth-child(3) {
  border-right: .15em solid #fff; }

app-dashboard .logs .stats-value {
  font-size: 80px;
  margin-top: 10px;
  color: #fff; }

app-dashboard .logs .stats-title {
  font-size: 16px;
  font-weight: 600;
  color: #fff;
  margin-bottom: 30px; }

app-dashboard .logs .green {
  background-color: #ABD8BC; }

app-dashboard .logs .blue {
  background-color: #A9D4EE; }

app-dashboard .logs .orange {
  background-color: #F4CB9E; }

app-dashboard .support {
  background-color: #e6e6e6;
  font-size: 14px; }

app-dashboard .support .header {
  font-size: 18px;
  font-weight: 600; }

app-dashboard .support a {
  text-decoration: none;
  text-transform: capitalize; }

app-dashboard .support .search input {
  padding: 10px;
  border: 0;
  font-size: 14px; }

app-dashboard .support .search button {
  width: 45px;
  border: 0;
  border-radius: 0;
  background-color: #6B9CE6; }

app-dashboard .support .search button .fa {
  color: #e6e6e6; }

app-dashboard .support .sub-item {
  font-size: 12px;
  margin-left: 20px; }

app-dashboard a {
  text-decoration: none; }
