app-providers-list {
  color: #4d4d4d;
  /* Adapted from user-dashboard */ }
  app-providers-list .row {
    clear: both;
    margin-bottom: 4em; }
  app-providers-list .center {
    text-align: center; }
  app-providers-list .margin-top {
    border-top: 1px dashed #ccc;
    padding-top: 3em; }
  app-providers-list mat-list a {
    border-bottom: 1px dashed #aaa;
    padding: .6em 0;
    text-decoration: none;
    width: 100%; }
  app-providers-list mat-list span {
    display: block;
    float: right; }
  app-providers-list mat-list a:hover {
    border-bottom: 1px dashed #aaa;
    color: #EAA00C;
    padding: .6em 0; }
  app-providers-list mat-list > div > span {
    font-size: 16px; }
  app-providers-list h2 button {
    float: right; }
  app-providers-list h2 {
    line-height: 36px; }
  app-providers-list .add-provider-button {
    background: #67b3f5;
    text-transform: uppercase;
    display: block;
    width: 175px;
    align-items: center;
    font-family: Raleway, SansSerif;
    color: white;
    font-weight: bold;
    margin: auto; }
